/* -----------------------------------  navbar ---------------------------- */
.Estilo_Header{
	width: 100%;
}
header nav{
	width: 100%;
	display:flex;
	justify-content: center;
	background: #fff;
	position: fixed;
	z-index: 1;
}
.menu_bar{
	display: none;
	
}
header ul{
	overflow: hidden;
	list-style: none;
}
header nav ul li{
	float:left;
}
header nav ul li a{
	color:#000000;
	padding:20px;
	display: block;
	text-decoration: none;
}
header ul li span{
	margin-right:10px;
}
header nav ul li a:hover{
	background: #347474;
}
.EstiloLogo{
	width: 140px;
	height:60px;
	display: flex;
	justify-content:center;
}
.contenedorNav {
	position: fixed;
	width: 100%;
	top: 0px;
	background: white;
	z-index: 2;
}
/* -----------------------------------  Nav Responsive ---------------------------- */
@media only screen and (max-width: 800px) {
header{
	position: fixed;
	z-index: 2;
	width: 100%;
}
header nav{
	position: fixed;
	width: 63%;
	height: 100%;
	margin: 0;
	z-index: 2;
}
header nav ul li{
	display:block ;
	float:none;
	border-bottom:1px solid rgba(255,255,255,.3);
}
.menu_bar{
	display: block;
	height: 100%;
	background:#fff ;
}
.menu_bar .bt-menu{
	display:block;
	padding:20px;
	text-decoration: none;
	font-weight: bold;
	font-size: 25px;
	box-sizing: border-box;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
}
.menu_bar span{
	display: flex;
	align-items: center;
	float: right;
}
.logonavbar {
	width: 135px;
	height: 35px;
	background: url('./assets/images/logoyoumagazine 2.png');
	background-repeat: no-repeat;
	background-size: 100% 145%;
	margin: 4px 5px 10px 10px;
	padding:20px;
	float: left;

}
}
/* -----------------------------------  componente Header ---------------------------- */

.cover {
	background-repeat: no-repeat;
	min-height: 100vh;
	position: relative;
	background-image: url('./assets/images/imagen-header-youmagazine-hombre-sosteniendo-celular.jpg');
	background-repeat: no-repeat;
	background-size: cover;


}

.cover_cover_alineacion_mx {
	position: absolute;
	float: left;
	margin: 120px 0px 0px 10px;
	top: 0%;
	left: 10%;
	width: 40%;
}

.cover_cover_alineacion_cell {
	position: absolute;
	top: 5%;
	right: 10%;
	width: 50%;
}


.cover_alineacion_titulo {
	width: 50%;
	position: absolute;
	top: 45%;
	left: 10%;

}


.cover_alineacion_titulo h1 {
	color: white;
	text-align: justify;
	font-size: 25px;
	font-weight: bold;
	margin: 10px;
	margin-top: 30px;
	text-decoration: none;
	text-shadow: 1px 0 0 #000, -1px 0 0 #000, 0 1px 0 #000, 0 -1px 0 #000, 1px 1px #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000;
}

.cover_alineacion {
	width: 60%;
	position: absolute;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
	top: 70%;
	left: 10%;
}


.texto {
	color: black;
	text-shadow: 0px 0 0 #fff, 0px 0 0 #fff, 0 0px 0 #fff, 0 0px 0 #fff, 1px 1px #fff, -1px -1px 0 #fff, 1px -1px 0 #fff, -1px 1px 0 #fff;
	font-size: 24px;
	text-decoration: none;
	font-weight: 400;
	text-align: center;
}

.cover_cover_alineacion_apps {
	width: 60%;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;
}

.cover_cover_alineacion_apps img {
	width: 40%;
	height: auto;
}

.desHeader {
	background: rgb(255, 255, 255);
	opacity: 0.8;
	font-size: 15px;
	text-align: center;
	width: 40%;
	color: black;
}

