/* ------------------------------- componente Contextos-------------------------- */

.landingPrecios {
	height: 100vh;
	box-sizing: border-box;
	clip-path: polygon(100% 0%, 100% 90%, 0% 100%, 0% 10%);
	background: linear-gradient(to right bottom, #FFB400, #FF00DF);
	box-sizing: border-box;
	text-align: justify;
}

.CardPrecios {
	margin: 1em;
	height: 72%;
	width: 30%;
	background: #ffffff;
	box-sizing: border-box;
	border-radius: 10px;
	box-sizing: border-box;
}

.estilo{
	padding: 3%;
	text-align: justify;
}
.divPrecio span {
	color: #ffffff;
	font-size: 20px;
}

.CardPrecios .CardBeneficiosIncluidos {
	color: black;
	padding: 1.1em;
	box-sizing: border-box;
}

.contextos {
	color: black;
	text-align: justify;
	text-justify: inter-word;
	margin: 3px 2px 2px 2px;
	padding: 3px 10px 5px 2px;
}

.corazon {

	font-size: 5rem;
	animation: latidos 3s infinite;
	transform-origin: center;
}


@keyframes latidos {
	from {
		transform: none;
	}

	50% {
		transform: scale(1.4);
	}

	to {
		transform: none;
	}
}

